import { Component } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Currency } from "./interfaces/currency";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
})
export class AppComponent {
  baseURL: string = "https://api.exchangeratesapi.io";
  options: string[] = [];
  selectedFrom: string = "EUR";
  selectedTo: string = "PLN";
  currencyData: any;
  translated: number = 0;
  actualValue: number = 1;
  rate: string = "";

  constructor(private http: HttpClient) { }

  async getData(baseCurr): Promise<Currency> {
    try {
      const response = await this.http
        .get(this.baseURL + `/latest?base=` + baseCurr)
        .toPromise();
      return response as Currency;
    } catch (error) {
      await this.handleError(error);
    }
  }

  handleError(error: any) {
    throw new Error("Error");
  }

  createOptions(base, rates): void {
    const keys = Object.keys(rates);
    const isExist = keys.find(e => e == base)

    if (isExist === undefined) {
      this.options = [base, ...keys]
    } else {
      this.options.push(...keys)
    }
  }

  onChangeValue(): void {
    this.getExchange();
  }

  async changeCurrency() {
    this.currencyData = await this.getData(this.selectedFrom);
    this.getExchange();
  }

  async getExchange() {
    const { rates } = this.currencyData;
    const ratesArray = rates;
    let value = 0;


    Object.keys(ratesArray).find((key) => {
      if (key == this.selectedTo) {
        value = ratesArray[key];
        return true;
      }
    });

    this.translated = parseFloat((value * this.actualValue).toFixed(2));
    this.rate = `${this.selectedFrom} = ${value} ${this.selectedTo}`
  }

  swapButton() {
    [this.selectedFrom, this.selectedTo] = [this.selectedTo, this.selectedFrom]
    this.changeCurrency()
  }

  //default inicjalization
  async ngOnInit() {
    this.currencyData = await this.getData(this.selectedFrom);
    const { base, rates } = this.currencyData;

    this.createOptions(base, rates);
    this.getExchange();
  }
}
